/* inject:imports */
/* endinject */

body,
html{
    height: 100%;
    font-family: Arial,Helvetica,sans-serif;
}
body{
    background-color: #fff;
    text-align: center;
    margin: 0;
}
.launchWrapper{
//    display: table;
//    height: 100%;
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 240px;
}
.launchContent{
//    display: table-cell;
//    vertical-align: middle;
    position: relative;
    top: -50%;
    display: none;
}
.indexLogo{
    display: block;
    margin: 0 auto;
    max-width: 100%;
}
.playBtn{
    margin: 0 auto;
    margin-bottom: 20px;
    width: 95px;
    height: 95px;
    background: url(../../course/content/UI/images/icon-play.png);
    cursor: pointer;
    &:not(.disabled){
        &:hover{
            background-position: -100% 0;
        }
    }
    &.disabled{
        cursor: default;
    }
}
.warningMessage{
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
}
.invisible{
    visibility: hidden;
}